export function Increment(counter) {
  return {
    type: "INCREMENT",
    payload: counter
  };
}

export function Decrement(counter) {
  return {
    type: "DECREMENT",
    payload: counter
  };
}

export const GetUsers = () => {
  //alert("here");
  return (dispatch) => {
    fetch("data.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      }
    })
      .then((response) => {
        return response.json();
      })
      .then(
        function (res) {
          //console.log(res);
          dispatch({
            type: "GET_USERS",
            payload: res
          });
        },
        function (error) {
          dispatch({
            type: "GET_USERS",
            payload: []
          });
        }
      );
  };
};
