const initialState = {
  counter: 0,
  users: []
};

export function counterReducer(state = initialState, action) {
  switch (action.type) {
    case "INCREMENT":
      return {
        ...state,
        counter: ++state.counter
      };
    case "DECREMENT":
      return {
        ...state,
        counter: --state.counter
      };
    case "GET_USERS":
      return {
        ...state,
        users: action.payload
      };
    default:
      return state;
  }
}
